
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Icon from 'vux/src/components/icon/index.vue'

import { SET_TITLE } from '@/store/types'
export default {
name: 'basecontainer',
computed: {
selected() {
this.$store.commit(SET_TITLE, this.$route.name)
return this.$route.path
}
},
methods: {
change(val) {
let p = val === 0 ? '/' : (val === 1 ? '/menu' : '/personal')
this.$router.replace({ path: p })
}
},
components: {
Tabbar,
TabbarItem,
Icon
}
}



